import React from 'react';
import '../Styles/WarrantyBadgeSection.css'; // Ensure you have the correct path to your CSS file
import image from '../images/card1.png';
import image2 from '../images/card2.png';
import image3 from '../images/card3.png';
import image4 from '../images/card5.png';

const WarrantyBadgeRow = () => {
  // In a real app, these would come from props or an API
  const warrantyBadges = [
    {
      id: 1,
      title: "GAF Lifetime Warranty",
      image: image // Replace with your image path
    },
    {
      id: 2,
      title: "LIFETIME Guarantee",
      image: image2 // Replace with your image path
    },
    {
      id: 3,
      title: "Premium Protection",
      image: image3 // Replace with your image path
    },
    {
      id: 4,
      title: "Elite Coverage",
      image: image4 // Replace with your image path
    }
  ];

  return (
    <div className="warranty-badge-row">
      <div className="badge-scroll-container">
        {warrantyBadges.map((badge) => (
          <div key={badge.id} className="badge-item">
            <img 
              src={badge.image} 
              alt={badge.title}
              className="badge-image"
              onError={(e) => {
                // Fallback if image fails to load
                e.currentTarget.alt = badge.title;
                e.currentTarget.parentElement!.textContent = badge.title;
              }}
            />
            
          </div>
        ))}
      </div>
    </div>
  );
};

export default WarrantyBadgeRow;