// pages/index.tsx
import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
import { Helmet } from "react-helmet";
import Header from "../Components/header";
import HeroSection from "../Components/hero";
import TestimonialSlider from "../Components/testimonial";
import ServiceCard from "../Components/Services";
import NewsletterSubscription from "../Components/NewsletterSubscription";
import Footer from "../Components/Footer";
import FAQSection from "../Components/Faq";
import ProjectManagementSection from "../Components/procces";
import AboutSection from "../Components/about";
import RecentProjects from "../Components/projects";
import ExitIntentPopup from "../Components/Exit";
import WarrantyBadgeSection from "../Components/WarrantyBadgeSection";

const IndexPage: React.FC<PageProps> = () => {
  return (
    <>
     <Helmet>
        <title>Bravo Roofing | Trusted Roofing Experts in Idaho</title>
        <meta
          name="description"
          content="Looking for roofing services in Idaho? Bravo Roofing offers top-notch roofing solutions with 57 perfect 5-star reviews. Get your free estimate today."
        />
        <meta
          property="og:title"
          content="Bravo Roofing | Trusted Roofing Experts in Idaho"
        />
        <meta
          property="og:description"
          content="Bravo Roofing is your go-to source for reliable roofing services in Idaho."
        />
        <meta
          property="og:image"
          content="https://preview.bravoroofingidaho.com/best.png" // Update with your actual image path
        />
        <meta
          property="og:url"
          content="https://preview.bravoroofingidaho.com/roofing-in-83714/"
        />
        <meta name="twitter:card" content="summary_large_image" />
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "LocalBusiness",
              "name": "Bravo Roofing Idaho",
              "address": {
                "@type": "PostalAddress",
                "addressLocality": "Garden City",
                "postalCode": "83714",
                "addressRegion": "ID",
                "addressCountry": "US"
              },
              "areaServed": {
                "@type": "GeoCircle",
                "geoMidpoint": {
                  "@type": "GeoCoordinates",
                  "latitude": 43.6414,
                  "longitude": -116.2700
                },
                "geoRadius": 5000
              },
              "telephone": "(208) 555-1234",
              "url": "https://preview.bravoroofingidaho.com/"
            }
          `}
        </script>
      </Helmet>
      <ExitIntentPopup />
      <Header />
      <HeroSection />
      <WarrantyBadgeSection />
      <TestimonialSlider />
      <ServiceCard />
      <AboutSection />
      <ProjectManagementSection />
      <FAQSection />
      <RecentProjects />
      <Footer />
    </>
  );
};

export default IndexPage;

// ‚úÖ Head tags for SEO + social preview

