import React, { useState, useEffect, useRef } from 'react';
import { FaChevronLeft, FaChevronRight, FaSearchPlus, FaTimes } from 'react-icons/fa';
import '../Styles/projects.css';

// Importing all project images
import project1 from '../images/project1.jpg';
import project2 from '../images/project2.jpg';
import project3 from '../images/project3.jpg';
import project4 from '../images/project4.jpg';
import project5 from '../images/project5.jpg';
import project6 from '../images/project6.jpg';
import project7 from '../images/project7.jpg';
import project8 from '../images/project8.jpg';
import project9 from '../images/project9.jpg';
import project10 from '../images/project10.jpg';
import project11 from '../images/project11.jpg';
import project12 from '../images/project12.jpg';
import project13 from '../images/project13.jpg';
import project14 from '../images/project14.jpg';
import project15 from '../images/project15.jpg';
import project16 from '../images/project16.webp';
import project17 from '../images/project17.webp';
import project18 from '../images/project18.webp';
import project19 from '../images/project19.webp';
import project20 from '../images/project20.webp';

// Project names for captions
const projectNames = [
  "Modern Architecture",
  "Urban Landscape",
  "Luxury Interior",
  "Coastal Retreat",
  "Mountain Adventure",
  "Industrial Design",
  "Tech Innovation",
  "Minimalist Space",
  "Nature Photography",
  "Creative Studio",
  "Urban Development",
  "Sustainable Design",
  "Luxury Residence",
  "Commercial Complex",
  "Landscape Design",
  "Modern Office",
  "Residential Tower",
  "Public Space",
  "Cultural Center",
  "Innovative Structure"
];

const PortfolioSlider = () => {
  // Create an array of all imported images
  const images = [
    project1, project2, project3, project4, project5,
    project6, project7, project8, project9, project10,
    project11, project12, project13, project14, project15,
    project16, project17, project18, project19, project20
  ];
  
  const [currentIndex, setCurrentIndex] = useState(0);
  const [itemsPerView, setItemsPerView] = useState(3);
  const [hoveredItem, setHoveredItem] = useState<number | null>(null);
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [lightboxIndex, setLightboxIndex] = useState(0);
  const autoSlideRef = useRef<NodeJS.Timeout | null>(null);

  // Handle responsive layout
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 576) {
        setItemsPerView(1);
      } else if (window.innerWidth <= 992) {
        setItemsPerView(2);
      } else {
        setItemsPerView(3);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Auto slide functionality
  useEffect(() => {
    if (autoSlideRef.current) {
      clearInterval(autoSlideRef.current);
    }
    
    autoSlideRef.current = setInterval(() => {
      goToNext();
    }, 5000);
    
    return () => {
      if (autoSlideRef.current) {
        clearInterval(autoSlideRef.current);
      }
    };
  }, [itemsPerView]);

  const goToNext = () => {
    setCurrentIndex(prevIndex => 
      (prevIndex + itemsPerView) >= images.length ? 0 : prevIndex + itemsPerView
    );
  };

  const goToPrev = () => {
    setCurrentIndex(prevIndex => 
      prevIndex - itemsPerView < 0 ? 
      Math.floor((images.length - 1) / itemsPerView) * itemsPerView : 
      prevIndex - itemsPerView
    );
  };

  // Calculate visible images
  const visibleImages = images.slice(currentIndex, currentIndex + itemsPerView);
  // Add filler images if needed to maintain consistent layout
  const fillerCount = itemsPerView - visibleImages.length;
  const fillerImages = Array(fillerCount).fill(null);
  
  // Reset hover state when slides change
  useEffect(() => {
    setHoveredItem(null);
  }, [currentIndex]);

  // Open lightbox
  const openLightbox = (index: number) => {
    setLightboxIndex(index);
    setLightboxOpen(true);
    document.body.style.overflow = 'hidden';
  };

  // Close lightbox
  const closeLightbox = () => {
    setLightboxOpen(false);
    document.body.style.overflow = 'auto';
  };

  // Navigate to next image in lightbox
  const nextLightboxImage = () => {
    setLightboxIndex(prev => (prev + 1) % images.length);
  };

  // Navigate to previous image in lightbox
  const prevLightboxImage = () => {
    setLightboxIndex(prev => (prev - 1 + images.length) % images.length);
  };

  // Handle keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (!lightboxOpen) return;
      
      if (e.key === 'Escape') {
        closeLightbox();
      } else if (e.key === 'ArrowRight') {
        nextLightboxImage();
      } else if (e.key === 'ArrowLeft') {
        prevLightboxImage();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [lightboxOpen]);

  return (
    <div className="portfolio-slider-container" id='portfolio'>
      <div className="portfolio-header">
        <h2>Our Creative Portfolio</h2>
        <p>Explore our latest projects and design solutions that showcase our expertise</p>
      </div>
      
      <div 
        className="portfolio-slider"
        onMouseEnter={() => {
          if (autoSlideRef.current) {
            clearInterval(autoSlideRef.current);
            autoSlideRef.current = null;
          }
        }}
        onMouseLeave={() => {
          if (!autoSlideRef.current) {
            autoSlideRef.current = setInterval(() => {
              goToNext();
            }, 5000);
          }
        }}
      >
        <button className="slider-btn prev" onClick={goToPrev} aria-label="Previous slide">
          <FaChevronLeft />
        </button>
        
        <div className="slider-items">
          {visibleImages.map((image, index) => {
            const absoluteIndex = currentIndex + index;
            return (
              <div 
                key={`${absoluteIndex}`}
                className="slider-item"
                onMouseEnter={() => setHoveredItem(absoluteIndex)}
                onMouseLeave={() => setHoveredItem(null)}
                onClick={() => openLightbox(absoluteIndex)}
              >
                <img 
                  src={image} 
                  alt={`Project ${absoluteIndex + 1}`} 
                  className="portfolio-image"
                />
                <div className={`image-overlay ${hoveredItem === absoluteIndex ? 'active' : ''}`}>
                  <FaSearchPlus className="preview-icon" />
                  <span className="image-number">{absoluteIndex + 1}/{images.length}</span>
                </div>
              </div>
            );
          })}
          
          {fillerImages.map((_, index) => (
            <div 
              key={`filler-${index}`}
              className="slider-item filler"
              aria-hidden="true"
            />
          ))}
        </div>
        
        <button className="slider-btn next" onClick={goToNext} aria-label="Next slide">
          <FaChevronRight />
        </button>
      </div>
      
      <div className="slider-dots">
        {Array.from({ length: Math.ceil(images.length / itemsPerView) }).map((_, index) => (
          <button
            key={index}
            className={`dot ${index === Math.floor(currentIndex / itemsPerView) ? 'active' : ''}`}
            onClick={() => setCurrentIndex(index * itemsPerView)}
            aria-label={`Go to slide ${index + 1}`}
          />
        ))}
      </div>
      
      {/* Lightbox */}
      {lightboxOpen && (
        <div className="lightbox active">
          <div className="lightbox-content">
            <img 
              src={images[lightboxIndex]} 
              alt={`Project ${lightboxIndex + 1}`} 
              className="lightbox-image"
            />
            <div className="lightbox-caption">
              {projectNames[lightboxIndex]}
            </div>
            <button className="lightbox-close" onClick={closeLightbox}>
              <FaTimes />
            </button>
            <button className="lightbox-nav lightbox-prev" onClick={prevLightboxImage}>
              <FaChevronLeft />
            </button>
            <button className="lightbox-nav lightbox-next" onClick={nextLightboxImage}>
              <FaChevronRight />
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default PortfolioSlider;