// TestimonialSlider.tsx
import React, { useState, useEffect, useRef } from 'react';
import '../Styles/TestimonialSlider.css';

interface Testimonial {
  id: number;
  content: string;
  author: string;
  role: string;
  timeAgo: string;
  avatar: string;
}

interface Stat {
  id: number;
  value: number;
  title: string;
  desc: string;
  suffix?: string;
  prefix?: string;
  plus?: boolean;
}

const TestimonialSlider: React.FC = () => {
const testimonials: Testimonial[] = [
  {
    id: 1,
    content: "Bravo Roofing was professional and efficient from start to finish. They completed our roof in just two days and left the site spotless. I highly recommend them to anyone in the Boise area.",
    author: "Sarah Johnson",
    role: "Homeowner, Boise",
    timeAgo: "2 days ago",
    avatar: "https://randomuser.me/api/portraits/women/44.jpg"
  },
  {
    id: 2,
    content: "We needed a full roof replacement in Meridian and Bravo delivered beyond our expectations. Their crew was friendly, punctual, and the new roof looks amazing.",
    author: "Michael Chen",
    role: "Business Owner, Meridian",
    timeAgo: "1 week ago",
    avatar: "https://randomuser.me/api/portraits/men/32.jpg"
  },
  {
    id: 3,
    content: "After the last windstorm, we had damage on our roof. Bravo Roofing came out quickly and handled everything professionally. We live in Nampa and they were here fast.",
    author: "Jessica Williams",
    role: "Resident, Nampa",
    timeAgo: "3 days ago",
    avatar: "https://randomuser.me/api/portraits/women/68.jpg"
  },
  {
    id: 4,
    content: "We had Bravo install a new roof on our rental property in Caldwell. They walked us through every step and delivered high-quality work at a fair price.",
    author: "David Rodriguez",
    role: "Property Manager, Caldwell",
    timeAgo: "4 days ago",
    avatar: "https://randomuser.me/api/portraits/men/65.jpg"
  },
  {
    id: 5,
    content: "Bravo helped us replace our aging roof in Eagle. From the quote to the final inspection, everything was smooth and stress-free. Excellent service!",
    author: "Emma Thompson",
    role: "Homeowner, Eagle",
    timeAgo: "1 week ago",
    avatar: "https://randomuser.me/api/portraits/women/26.jpg"
  }
];


const statsData: Stat[] = [
  {
    id: 1,
    value: 57,
    title: "Roofs Completed",
    desc: "In Boise & Surrounding Areas"
  },
  {
    id: 2,
    value: 100,
    title: "5-Star Reviews",
    desc: "From Every Verified Customer",
    suffix: "%"
  },
  {
    id: 3,
    value: 30,
    title: "ZIP Codes Served",
    desc: "Across the Treasure Valley",
    plus: true
  }
];


  const sliderRef = useRef<HTMLDivElement>(null);
  const statsRef = useRef<HTMLDivElement>(null);
  const [displayedStats, setDisplayedStats] = useState(statsData.map(stat => ({
    ...stat,
    displayedValue: 0
  })));
  
  // Initialize animation
  useEffect(() => {
    const slider = sliderRef.current;
    if (!slider) return;
    
    // Create a smooth animation using CSS transitions
    slider.style.transition = 'transform 0.6s cubic-bezier(0.22, 0.61, 0.36, 1)';
    
    const animate = () => {
      if (slider) {
        const scrollAmount = 1;
        slider.scrollLeft += scrollAmount;
        
        // Reset to beginning when reaching end
        if (slider.scrollLeft >= slider.scrollWidth / 2) {
          slider.style.transition = 'none';
          slider.scrollLeft = 0;
          // Force reflow to apply transition reset
          void slider.offsetWidth;
          slider.style.transition = 'transform 0.6s cubic-bezier(0.22, 0.61, 0.36, 1)';
        }
      }
      
      requestAnimationFrame(animate);
    };
    
    animate();
    
    return () => {
      // Cleanup if needed
    };
  }, []);

  // Animate stats on scroll
  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            setDisplayedStats(prevStats => 
              prevStats.map(stat => {
                const duration = 2000;
                const startTime = Date.now();
                const startValue = 0;
                const endValue = stat.value;
                
                const animate = () => {
                  const elapsed = Date.now() - startTime;
                  const progress = Math.min(elapsed / duration, 1);
                  const easeProgress = 1 - Math.pow(1 - progress, 3);
                  const currentValue = startValue + (endValue - startValue) * easeProgress;
                  
                  setDisplayedStats(prev => 
                    prev.map(s => 
                      s.id === stat.id 
                        ? {...s, displayedValue: currentValue} 
                        : s
                    )
                  );
                  
                  if (progress < 1) {
                    requestAnimationFrame(animate);
                  } else {
                    setDisplayedStats(prev => 
                      prev.map(s => 
                        s.id === stat.id 
                          ? {...s, displayedValue: endValue} 
                          : s
                      )
                    );
                  }
                };
                
                requestAnimationFrame(animate);
                return stat;
              })
            );
          }
        });
      },
      { threshold: 0.2 }
    );

    if (statsRef.current) {
      observer.observe(statsRef.current);
    }

    return () => {
      if (statsRef.current) {
        observer.unobserve(statsRef.current);
      }
    };
  }, []);

  return (
    <div className="testimonial-slider-container" id="reviews">
      <div className="slider-header">
        <h2>Customer Testimonials</h2>
        <p>Discover what our clients say about working with us. We take pride in our exceptional service.</p>
      </div>
      
      <div 
        className="testimonial-slider"
        ref={sliderRef}
      >
        {[...testimonials, ...testimonials].map((testimonial, index) => (
          <div key={`${testimonial.id}-${index}`} className="testimonial-card">
            <div className="quote-icon">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                <path d="M14.017 21v-7.391c0-5.704 3.731-9.57 8.983-10.609l.995 2.151c-2.432.917-3.995 3.638-3.995 5.849h4v10h-9.983zm-14.017 0v-7.391c0-5.704 3.748-9.57 9-10.609l.996 2.151c-2.433.917-3.996 3.638-3.996 5.849h3.983v10h-9.983z"/>
              </svg>
            </div>
            <p className="testimonial-content">{testimonial.content}</p>
            <div className="rating">
              {[...Array(5)].map((_, i) => (
                <svg key={i} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                  <path d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"/>
                </svg>
              ))}
            </div>
            <div className="client-info">
              <div className="avatar">
                <img src={testimonial.avatar} alt={testimonial.author} />
              </div>
              <div className="client-details">
                <h3>{testimonial.author}</h3>
                <p>{testimonial.role} ‚Ä¢ {testimonial.timeAgo}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
      
      <div className="stats-container" ref={statsRef}>
  {displayedStats.map(stat => (
    <div key={stat.id} className="stat-card">
      <div className="stat-content">
       <div className="stat-value">
  {stat.prefix || ''}
  {stat.suffix === '%'
    ? `${Math.round(stat.displayedValue)}${stat.suffix}`
    : `${Math.floor(stat.displayedValue).toLocaleString()}${stat.plus && stat.displayedValue >= stat.value ? '+' : ''}`}
</div>

        <div className="stat-title">{stat.title}</div>
        <p className="stat-desc">{stat.desc}</p>
      </div>
    </div>
  ))}
</div>

    </div>
  );
};

export default TestimonialSlider;