'use client';

import { useEffect, useState, useRef } from 'react';
import React from 'react';
import '../Styles/exit.css';

const ExitIntentPopup = () => {
  const [showPopup, setShowPopup] = useState(false);
  const hasPopupTriggered = useRef(false); // Tracks if popup has been triggered
  const idleTimeout = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    if (hasPopupTriggered.current) return;

    const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0;

    // Desktop exit intent handler
    const handleMouseLeave = (e: MouseEvent) => {
      if (!isTouchDevice && e.clientY <= 0 && !hasPopupTriggered.current) {
        triggerPopup();
      }
    };

    // Mobile idle timeout
    if (isTouchDevice) {
      idleTimeout.current = setTimeout(() => {
        if (!hasPopupTriggered.current) triggerPopup();
      }, 30000);
    }

    document.addEventListener('mouseleave', handleMouseLeave);

    return () => {
      document.removeEventListener('mouseleave', handleMouseLeave);
      if (idleTimeout.current) clearTimeout(idleTimeout.current);
    };
  }, []);

  const triggerPopup = () => {
    hasPopupTriggered.current = true;
    setShowPopup(true);
  };

  const handleClose = () => {
    setShowPopup(false);
  };

  if (!showPopup) return null;

  return (
    <div className="exit-popup-overlay">
      <div className="exit-popup">
        <h2>Wait! Before You Go...</h2>
        <p>Don't miss out on our limited-time offer!</p>
        <button onClick={handleClose}>Close</button>
      </div>
    </div>
  );
};

export default ExitIntentPopup;